.cart{
    border: 1px solid black;
    height: 240px;
    width: 250px;
    padding: 10px;
    text-align: center;
    background-color: aliceblue;  
}
.cart img{
    border-radius: 50%;
}
.container{
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    margin: 30px;
}

.cartItem{
    margin: 40px;
    height: 300px;
    width: 250px;
}